@import "../../_globalColor";

.team-heading {
  font-size: 56px;
  font-weight: 400;
  margin: 0;
  line-height: normal;
}

.team-subtitle {
  margin: 0;
  margin-bottom: 50px;
  text-transform: uppercase;
}

.team-category {
  margin-top: 60px;
  margin-bottom: 60px;
}

.team-subcategory {
  margin-top: 40px;
  margin-bottom: 30px;
}

.team-subheading {
  font-size: 32px;
  margin-bottom: 30px;
  text-align: center;
}

.team-subsubheading {
  font-size: 24px;
  margin-bottom: 20px;
  text-align: center;
  font-weight: 500;
}

.team-main-div {
  width: 100%;
  padding: 20px 10px;
  position: relative;
  z-index: 1;
}

.team-cards-div {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
  gap: 1.5rem;
  max-width: 1400px;
  margin: 0 auto;
  min-height: 200px;
}

/* Responsive */
@media (max-width: 768px) {
  .team-heading {
    font-size: 32px;
    text-align: center;
  }
  .team-subheading {
    font-size: 22px;
  }
  .team-subsubheading {
    font-size: 18px;
  }
  .team-subtitle {
    text-align: center;
    font-size: 16px;
  }
  .team-cards-div {
    gap: 1rem;
  }
}
