@import "../../_globalColor";

.main {
  width: 90%;
  padding: 20px 10px;
  margin: 0px auto;
  margin-top: 4rem;
}
.greet-main {
  width: 90%;
  padding: 20px 10px;
  margin: 0px auto;
  margin-top: 2rem;
}

.greeting-main {
  display: flex;
  // align-items: center;
  position: relative; /* allow absolutely positioned background video */
  /* Ensure the greeting section is tall enough for the background video
     and clip any overflowing parts so following sections are not overlapped. */
  min-height: 80vh;
  overflow: hidden;
}

.subTitle {
  color: $subTitle !important;
}

.greeting-main {
  display: flex;
}

.greeting-main > * {
  margin-bottom: 30px;
}

/* Explicit column sizing: text ~55%, media ~45% */
.greeting-text-div {
  flex: 1 1 55%;
}
.greeting-image-div {
  flex: 0 0 45%;
  max-width: 55%;
  position: relative;
  z-index: 1; /* sit above the background video when present */
}

/* Background video that sits under the text */
.greeting-video-bg {
  position: absolute;
  top: 0;
  right: 0;
  /* Make the video cover roughly half the viewport height so it's visible
     even if the greeting container is short. Use viewport units for predictable
     sizing across pages. */
  height: 80vh;
  width: 100%;
  object-fit: cover;
  z-index: 0;
  pointer-events: none; /* allow clicks through to text/links */
}

.greeting-text-div {
  z-index: 2; /* ensure text is above the background video */
}

.empty-div {
  margin-bottom: 40px;
}

.button-greeting-div {
  display: flex;
  margin-top: 40px;
}
.download-link-button {
  text-decoration: none;
}

.greeting-text {
  font-size: 70px;
  line-height: 1.1;
  color: $textColor !important;
}

.greeting-text-p {
  font-size: 30px;
  line-height: 40px;
}

/* Dark-mode overrides for greeting text to ensure strong contrast */
.dark-mode.greeting-text {
  color: $textColorDark !important;
}

.dark-mode.greeting-text-p {
  color: $textColorDark !important;
}

.greeting-image-div > img {
  max-width: 100%;
  height: auto;
}

.greeting-image-div > .greeting-video,
.greeting-image-div > video {
  width: 100%;
  max-width: 100%;
  height: auto;
  object-fit: contain;
  display: block;
}

.wave-emoji {
  animation-duration: 1.8s;
  animation-iteration-count: infinite;
  animation-name: wave;
  display: inline-block;
  transform-origin: 70% 70%;
}

@keyframes wave {
  0% {
    transform: rotate(0deg);
  }
  10% {
    transform: rotate(-10deg);
  }
  20% {
    transform: rotate(12deg);
  }
  30% {
    transform: rotate(-10deg);
  }
  40% {
    transform: rotate(9deg);
  }
  50% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

/* Media Query */
@media (max-width: 1380px) {
  .greeting-text {
    font-size: 50px;
  }
  .greeting-text-p {
    font-size: 20px;
  }
}
@media (max-width: 768px) {
  .button-greeting-div {
    justify-content: space-around;
  }
  .greeting-text {
    font-size: 30px;
    text-align: center;
  }
  .greeting-text-p {
    font-size: 16px;
    line-height: normal;
    text-align: center;
  }
  .greeting-main {
    display: block;
  }
  .greeting-image-div {
    width: 100%;
    height: 256px;
    max-width: 100%;
  }
  .greeting-image-div > video {
    width: 100%;
    height: 100%;
    object-fit: cover; /* fill area on small screens */
  }
  /* On small screens, don't keep the absolute background layout */
  .greeting-video-bg {
    position: static;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

@media (max-width: 320px) {
  .main {
    width: auto;
  }
}
