@import "../../_globalColor";

.card-image {
  max-width: 100%;
  height: auto;
  width: 180px;
  max-height: 180px;
  object-fit: cover;
  border-radius: 8px;
  display: block;
}
.card-title {
  color: $titleColor;
  font-size: 1rem;
  line-height: 1.4;
  font-weight: 700;
  margin: 12px 0 8px 0;
}
.card-subtitle {
  color: $cardSubtitle;
  font-size: 0.875rem;
  line-height: 1.4;
  margin: 0 0 6px 0;
}
.card-advisor {
  color: $subTitle;
  font-size: 0.75rem;
  line-height: 1.3;
  margin: 6px 0 0 0;
  font-style: italic;
}
.certificate-card {
  background-color: $lightBackground2 !important;
  box-shadow: $lightBoxShadowDark 0px 10px 30px -15px !important;
  padding: 1rem;
  border-radius: 10px;
  border: 1px solid $lightBorder1 !important;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
  width: 200px !important;
  min-width: 200px !important;
  max-width: 200px !important;
  min-height: 350px;
  display: flex !important;
  flex-direction: column;
  position: relative;
  visibility: visible !important;
  opacity: 1 !important;
  z-index: 1;
}
.certificate-detail-div {
  text-align: center;
  flex: 1;
  display: flex;
  flex-direction: column;
}
.certificate-card:hover {
  box-shadow: $lightBoxShadowDark 0px 20px 30px -10px;
  transform: translateY(-4px);
}
.dark-mode.certificate-card:hover {
  box-shadow: $teamCertificateCardHoverDark 0px 20px 30px -10px;
}
.certificate-image-div {
  position: relative;
  height: 180px;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 8px;
  background-color: #f5f5f5;
  border-radius: 8px;
}
.dark-mode .certificate-image-div {
  background-color: rgba(255, 255, 255, 0.05);
}
.certificate-card-footer {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 8px;
  padding-top: 8px;
  border-top: 1px solid $lightBorder1;
}
.dark-mode .certificate-card-footer {
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}
.certificate-card-footer span.certificate-tag {
  background: $buttonColor;
  color: $lightBackground3;
  vertical-align: middle;
  align-items: center;
  border-radius: 4px;
  display: inline-flex;
  font-size: 0.7rem;
  height: 1.8em;
  justify-content: center;
  white-space: nowrap;
  line-height: 1.5;
  margin: 0 0.25rem 0.25rem 0;
  padding: 0 0.6em;
  cursor: pointer;
  transition: 0.2s ease-in;
}
span.certificate-tag:hover {
  background: $buttonHover;
}

/* Dark mode styles */
.dark-mode.certificate-card {
  background-color: $darkBackground;
  border: 1px solid rgba(255, 255, 255, 0.1);
}
.dark-mode .card-title {
  color: $textColorDark;
}
.dark-mode .card-subtitle {
  color: rgba(255, 255, 255, 0.7);
}
.dark-mode .card-advisor {
  color: rgba(255, 255, 255, 0.5);
}
