@import "../../_globalColor";

.dev-icons {
  padding-left: 0;
  list-style: none;
  font-size: 2.0rem;
  margin-bottom: 0px;
  margin-top: 0px;
  text-align: center;
}

.research-area-inline {
  display: inline-block;
  margin-right: 15px;
  margin-bottom: 15px;
}

.research-area-inline > i {
  color: $subTitle;
  font-size: 2.0rem;
}

.research-area-inline > i:hover {
  color: $areasColor;
}
.research-area-inline > p {
  color: $subTitle;
  font-size: 18px;
}
.research-area-inline > i:hover ~ p {
  color: $areasColor;
}
